/*
 * Gutenberg Editor Styles
 */

/** === Includes === */

@charset 'utf-8';

@import 'foundation';
@import 'settings/settings';

@import 'foundationpress-settings';

$dark-color: #333;
$light-color: #fff;

@import 'functions';

@import 'settings-overrides';

@include foundation-typography;
@include foundation-button;
@include foundation-table;

@import 'mixins';

@import 'global/wp-overrides';

@import "components/links";

/** === Base Typography === */
body {
    font-size: $global-font-size;
    font-family: $body-font-family;
    color: $body-font-color;
}

/** === Post Title === */
.editor-post-title__block .editor-post-title__input{
  @extend h1;
}

/** === Button === */
.wp-block-button {

    // add general foundation button styling to button in editor
    .wp-block-button__link {
        @include button( false, get-color( 'primary' ), 'auto', 'auto' );
    }

    // add special styling for squared buttons
    &.is-style-squared .wp-block-button__link {
        border-radius: 0;
    }

    // add outline styles
    &.is-style-outline .wp-block-button__link{
        @extend .hollow;
    }

    // set transparent background to block for outline button
    &.is-style-outline,
    &.is-style-outline:hover,
    &.is-style-outline:focus,
    &.is-style-outline:active {
        background: transparent;
    }
}

/** === File === */
.wp-block-file__button{
    @include button();
}

/** === Pullquote === */
.wp-block-paragraph.has-background{
    padding: rem-calc(rem-calc( 20 ) rem-calc( 30 ));
}

/** === Table === */
.wp-block-table td{
    border: none;
}

/** === Latest Posts grid view === */
.wp-block-latest-posts.is-grid{
    list-style: none;
}

:root > .is-root-container {

    & > .wp-block {

        &:not( .alignwide ) {

            margin-top: rem-calc( 16 );

            @include breakpoint( medium ) {
                margin-top: rem-calc( 48 );
            }

        }

        &.alignwide:not( .wp-block-media-text ) {

            max-width: 100%;
            left: 0;
            right: 0;

        }

        &.wp-block-media-text {

            margin-top: rem-calc( 16 );

            @include breakpoint( medium ) {
                margin-top: rem-calc( 48 );
            }

        }

    }

}

// Account for some colors not being global in theme.json
@import 'global/gutenberg';

@import 'gutenberg/editor/cover';
@import 'gutenberg/editor/colors';
@import 'gutenberg/editor/columns';
@import "gutenberg/editor/typography";
@import "gutenberg/editor/button";
@import "gutenberg/editor/post-template";
@import "gutenberg/editor/post-author";
@import "gutenberg/editor/post-date";
@import "gutenberg/editor/embed";
@import "gutenberg/editor/image";
@import "gutenberg/editor/social-links";
@import "gutenberg/editor/query";